import React, { Component } from "react";
import Track from "./Track";

class Deck extends Component {
  componentDidMount() {
    this.tracks = [];
    for (let i = 0; i < this.props.samples.length; i++) {
      this.tracks.push(this.refs[this.getRef(i + 1)]);
    }
  }
  playBeat() {
    for (let i = 0; i < this.props.samples.length; i++) {
      this.tracks[i].playBeat();
    }
  }
  getRef(track) {
    return "track" + track;
  }
  render() {
    return (
      <div className="Deck">
        {this.props.samples.map((src, index) => {
          return (
            <Track
              src={src}
              currentBeat={this.props.currentBeat}
              ref={this.getRef(index + 1)}
            />
          );
        }, this)}
      </div>
    );
  }
}

export default Deck;



