import React, { Component } from "react";

class Audio extends Component {
  play() {
    this.refs.audio.play();
  }
  stop() {
    this.refs.audio.pause();
  }
  currentTime(ms) {
    this.refs.audio.currentTime = ms;
  }
  render() {
    return <audio src={this.props.src} ref="audio" />;
  }
}

export default Audio;



