import React, { Component } from "react";
import ControlPanel from "./Controls/ControlPanel";
import Deck from "./Deck/Deck";

const initialBpm = 120;

class Sampler extends Component {
  constructor(props) {
    super(props);
    this.state = {
      beat: 0,
      playing: false,
      bpm: initialBpm,
      samples: [
        "https://github.com/therudnick/therudnick.github.io/raw/master/mp3/kick.mp3",
        "https://github.com/therudnick/therudnick.github.io/raw/master/mp3/snare.mp3",
        "https://github.com/therudnick/therudnick.github.io/raw/master/mp3/hat-closed.mp3",
        "https://github.com/therudnick/therudnick.github.io/raw/master/tom-hi.mp3",
        "https://github.com/therudnick/therudnick.github.io/raw/master/tom-low.mp3"
      ]
    };
  }
  play() {
    this.setState({ playing: true });
    this.triggerNextBeat();
  }
  stop() {
    if (this.timeout) {
      clearTimeout(this.state.timeout);
    }
    this.setState({
      beat: 0,
      playing: false
    });
  }
  triggerNextBeat() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
    this.timeout = setTimeout(() => {
      this.nextBeat();
    }, 60000 / (4 * this.state.bpm));
  }
  nextBeat() {
    if (this.state.playing) {
      // update state
      if (this.state.beat === 16) {
        this.setState({ beat: 1 });
      } else {
        this.setState(prevState => {
          return { beat: prevState.beat + 1 };
        });
      }

      // send play command to deck
      this.refs.deck.playBeat();

      // reset timeout
      this.triggerNextBeat();
    } else {
      this.stop();
    }
  }
  updateBpm(value) {
    this.setState({ bpm: value });
  }
  render() {
    return (
      <div className="Sampler">
        <ControlPanel
          play={this.play.bind(this)}
          stop={this.stop.bind(this)}
          bpm={this.state.bpm}
          updateBpm={this.updateBpm.bind(this)}
        />
        <Deck
          samples={this.state.samples}
          currentBeat={this.state.beat}
          ref="deck"
        />
      </div>
    );
  }
}

export default Sampler;



