import React, { Component } from "react";
import ControlButton from "../Controls/ControlButton";
import Audio from "./Audio";
import Beat from "./Beat";

class Track extends Component {
  trigger() {
    this.refs.audio.stop();
    this.refs.audio.currentTime(0);
    this.refs.audio.play();
  }
  playBeat() {
    console.log("playBeat");
    if (this.refs[this.getRef(this.props.currentBeat)].state.on) {
      this.trigger();
    }
  }
  getRef(beat) {
    return "beat" + beat;
  }
  render() {
    return (
      <div className="row">
        <Audio src={this.props.src} ref="audio" />
        <button className="trigger" onClick={this.trigger.bind(this)}>
          <i class="fas fa-play" />
        </button>
        <Beat
          beat={1}
          playing={this.props.currentBeat === 1}
          ref={this.getRef(1)}
        />
        <Beat
          beat={2}
          playing={this.props.currentBeat === 2}
          ref={this.getRef(2)}
        />
        <Beat
          beat={3}
          playing={this.props.currentBeat === 3}
          ref={this.getRef(3)}
        />
        <Beat
          beat={4}
          playing={this.props.currentBeat === 4}
          ref={this.getRef(4)}
        />
        <Beat
          beat={5}
          playing={this.props.currentBeat === 5}
          ref={this.getRef(5)}
        />
        <Beat
          beat={6}
          playing={this.props.currentBeat === 6}
          ref={this.getRef(6)}
        />
        <Beat
          beat={7}
          playing={this.props.currentBeat === 7}
          ref={this.getRef(7)}
        />
        <Beat
          beat={8}
          playing={this.props.currentBeat === 8}
          ref={this.getRef(8)}
        />
        <Beat
          beat={9}
          playing={this.props.currentBeat === 9}
          ref={this.getRef(9)}
        />
        <Beat
          beat={10}
          playing={this.props.currentBeat === 10}
          ref={this.getRef(10)}
        />
        <Beat
          beat={11}
          playing={this.props.currentBeat === 11}
          ref={this.getRef(11)}
        />
        <Beat
          beat={12}
          playing={this.props.currentBeat === 12}
          ref={this.getRef(12)}
        />
        <Beat
          beat={13}
          playing={this.props.currentBeat === 13}
          ref={this.getRef(13)}
        />
        <Beat
          beat={14}
          playing={this.props.currentBeat === 14}
          ref={this.getRef(14)}
        />
        <Beat
          beat={15}
          playing={this.props.currentBeat === 15}
          ref={this.getRef(15)}
        />
        <Beat
          beat={16}
          playing={this.props.currentBeat === 16}
          ref={this.getRef(16)}
        />
      </div>
    );
  }
}

export default Track;



