import React, { Component } from "react";

class ControlInput extends Component {
  render() {
    return (
      <input
        type={this.props.type}
        min={this.props.min}
        max={this.props.max}
        value={this.props.value}
        onChange={this.props.onChange}
      />
    );
  }
}

export default ControlInput;



